import React, { useState } from 'react';
import { Box, Button, Typography, makeStyles, Grid, Dialog, IconButton, Radio  } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: theme.spacing(2),
  },
  radio: {
    position: 'absolute',
    top: theme.spacing(1),
    left: theme.spacing(1),
  },
  imageItem: {
    position: 'relative',
    margin: theme.spacing(1),
  },
  image: {
    width: '100%',
    height: '100%',
    objectFit: 'cover',
  },
  info: {
    position: 'absolute',
    bottom: 0,
    background: 'rgba(0,0,0,0.6)',
    color: '#fff',
    width: '100%',
    padding: theme.spacing(0.5, 0),
    textAlign: 'center',
  },
  buttonGroup: {
    display: 'flex',
    justifyContent: 'flex-center',
    width: '100%',
    margin: theme.spacing(2),
    paddingLeft: '15px'
  },
}));

const ResultBlock = () => {
    const classes = useStyles();
    const [open, setOpen] = useState(false); // state for managing the dialog open state
    const [selectedImage, setSelectedImage] = useState(null); // state for managing the selected image
    const [selectedCategory, setSelectedCategory] = useState(''); // state for managing selected category
  
    // Sample data, replace this with real data
    const results = Array.from({ length: 10 }, (_, index) => ({ similarityScore: '80%', categoryName: `Category ${index + 1}` }));
  
    const handleOpenDialog = (image) => {
      setSelectedImage(image);
      setOpen(true);
    };
  
    const handleCloseDialog = () => {
      setOpen(false);
    };
  
    const handleRadioChange = (event) => {
      setSelectedCategory(event.target.value);
    };
  
    const handleSelectForDetails = () => {
      console.log(selectedCategory);
      // Add your logic for sending selected category here
    };
  return (
    <Box className={classes.root}>
      <Typography variant="h5">Results</Typography>
      <Grid container spacing={2}>
        {results.map((result, index) => (
        <Grid item xs={12} sm={6} md={2} key={index} className={classes.imageItem}>
            <img className={classes.image} src="https://via.placeholder.com/100" alt="Result" onClick={() => handleOpenDialog("https://via.placeholder.com/100")} /> {/* Update the src attribute with your image URL */}
            <Radio color="primary" className={classes.radio} checked={selectedCategory === result.categoryName} onChange={handleRadioChange} value={result.categoryName} name="category" />
            <Box className={classes.info}>
                <Typography variant="caption">{result.similarityScore}</Typography>
                <Typography variant="caption">{result.categoryName}</Typography>
            </Box>
        </Grid>
        ))}
      </Grid>
      <Dialog open={open} onClose={handleCloseDialog} maxWidth="md" fullWidth>
        <IconButton style={{ position: 'absolute', right: 0, color: '#000' }} onClick={handleCloseDialog}>
          <CloseIcon />
        </IconButton>
        {selectedImage && <img src={selectedImage} alt="Selected" style={{width: '100%', height: 'auto'}} />}
      </Dialog>
      <Box className={classes.buttonGroup}>
        <Button variant="contained" color="primary" style={{marginRight: "10px"}} onClick={handleSelectForDetails}>Select for details</Button>
        <Button variant="contained" color="secondary">Search Again</Button>
      </Box>
    </Box>
  );
};

export default ResultBlock;
