import React, { useState } from 'react';
import { Box, Button, FormControl, InputLabel, Select, MenuItem, Typography, Dialog, IconButton } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';

const UploadBlock = () => {
  const [category, setCategory] = useState(''); // for managing the select box state
  const [uploadedImage, setUploadedImage] = useState(null); // placeholder for managing the uploaded image state
  const [open, setOpen] = useState(false); 
  const handleCategoryChange = (event) => {
    setCategory(event.target.value);
  };

  const handleImageUpload = (event) => {
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();
      
      reader.onload = function(e) {
        setUploadedImage(e.target.result);
      };

      reader.readAsDataURL(event.target.files[0]); // convert to base64 string
    }
  };

  const handleOpenDialog = () => {
    setOpen(true);
  };

  const handleCloseDialog = () => {
    setOpen(false);
  };

  return (
    <Box display="flex" flexDirection="column" alignItems="center" justifyContent="center">
      <FormControl variant="filled" style={{width: '100%'}}>
        <InputLabel>Category</InputLabel>
        <Select value={category} onChange={handleCategoryChange}>
            <MenuItem value={"All"}>All</MenuItem>
            <MenuItem value={"M02_Air_Con_System"}>M02_Air_Con_System</MenuItem>
            <MenuItem value={"M04_Belt"}>M04_Belt</MenuItem>
            <MenuItem value={"M05_Brake_System"}>M05_Brake_System</MenuItem>
            <MenuItem value={"M06_Cab_Body"}>M06_Cab_Body</MenuItem>
            <MenuItem value={"M07_Clutch_Parts"}>M07_Clutch_Parts</MenuItem>
            <MenuItem value={"M08_Diesel_Engine"}>M08_Diesel_Engine</MenuItem>
            <MenuItem value={"M09_Disc_Plate"}>M09_Disc_Plate</MenuItem>
            <MenuItem value={"M10_Electronic_System"}>M10_Electronic_System</MenuItem>
            <MenuItem value={"M12_Fuel_System"}>M12_Fuel_System</MenuItem>
            <MenuItem value={"M16_Starter_Alternator"}>M16_Starter_Alternator</MenuItem>
            <MenuItem value={"M18_Suspension"}>M18_Suspension</MenuItem>
            <MenuItem value={"M21_Workshop_Tools"}>M21_Workshop_Tools</MenuItem>
            <MenuItem value={"Packing"}>Packing</MenuItem>
            <MenuItem value={"stationary"}>stationary</MenuItem>
        </Select>
      </FormControl>

      <Box style={{width: '100%', display: 'flex', justifyContent: 'space-between', marginTop: '10px'}}>
        <Typography variant="body1">Upload Picture</Typography>
        <input accept="image/*" style={{ display: 'none' }} id="raised-button-file" multiple type="file" onChange={handleImageUpload} />
        <label htmlFor="raised-button-file">
          <Button variant="contained" component="span">
            Upload
          </Button>
        </label>
      </Box>

      <Box>
        <Typography variant="body1">Preview:</Typography>
        {/* Image preview area */}
        <Box display="flex" alignItems="center" justifyContent="center" bgcolor="grey.300" style={{width: '100%', height: '300px'}}>
            {uploadedImage ? 
            <img src={uploadedImage} alt="Uploaded" style={{maxWidth: '100%', maxHeight: '100%', objectFit: 'cover', cursor: 'pointer'}} onClick={handleOpenDialog} />
            :
            <Box style={{width: '100%', height: '100%', backgroundColor: 'grey'}}/>
            }
        </Box>
      </Box>

            {/* Dialog for showing full screen image */}
    <Dialog open={open} onClose={handleCloseDialog} maxWidth="md" fullWidth>
        <IconButton style={{ position: 'absolute', right: 0, color: '#000' }} onClick={handleCloseDialog}>
          <CloseIcon />
        </IconButton>
        <img src={uploadedImage} alt="Uploaded" style={{width: '100%', height: 'auto'}} />
    </Dialog>

    <Box display="flex" justifyContent="center" marginTop={2}>
        <Button variant="contained" color="primary" style={{width: '100%', marginTop: '10px'}}>Search</Button>
    </Box>
    </Box>
  );
};

export default UploadBlock;
