import React from 'react';
import { Typography, Box, Button, makeStyles, useTheme, useMediaQuery } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    height: '20vh',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    background: 'linear-gradient(180deg, #B2D8D8 0%, #ffffff 100%)',
    paddingRight: '30px',
    paddingLeft: '30px',
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'column',
      height: 'auto',
      paddingTop: '30px',
      paddingRight: '15px',
      paddingLeft: '15px',
      paddingBottom: '15px'
      
    },
  },
  titleContainer: {
    display: 'flex',
    flexDirection: 'column',
  },
  buttonContainer: {
    display: 'flex',
    flexDirection: 'column',
    [theme.breakpoints.down('sm')]: {
      marginTop: '15px',
      width: '60%'
    },
  },
}));

const Header = () => {
  const classes = useStyles();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <Box className={classes.root}>
      <Box className={classes.titleContainer}>
        <Typography variant={isMobile ? "h4" : "h2"}>Imagify Search</Typography>
        <Typography variant={isMobile ? "subtitle1" : "h6"}>Version 2.31</Typography>
      </Box>
      <Box className={classes.buttonContainer}>
        <Button variant="contained" color="primary" style={{marginTop: '10px', width: isMobile ? '100%' : '300px', padding: '40px'}} >Train </Button>
      </Box>
    </Box>
  )
}

export default Header;
